@use 'base/variables' as v;
@use 'base/mixins' as m;

@media (prefers-color-scheme: dark) {
    body {
        background-color: v.$grisDarkMode;
    }

    .sidebar {
        @include m.tablet {
            background-color: darken(v.$grisDarkMode, 7%);
        }
        .sidebar-nav a {
            &.activo,
            &:hover {
                background-color: darken(v.$naranja, 10%);
            }
        }
    }

    .subtitulo, 
    .formulario label,
    .login .acciones a,
    .crear .acciones a,
    .olvide .acciones a,
    .filtros h2,
    .filtros .campo label,
    .barra p,
    .formulario .campo input {
        color: v.$blanco;
    }
    .descripcion-pagina,
    .nombre-pagina {
        color: darken(v.$grisClaro, 15%);
    }
    .formulario .campo input{
        background-color: lighten(v.$grisDarkMode, 15%);
        &::placeholder {
            color: darken(v.$grisClaro, 30%);
        }
    }
    .barra-mobile,
    .barra {
        background-color: darken(v.$grisDarkMode, 7%);
    }
    .barra p span {
        color: v.$naranja;
    }
    .barra .cerrar-sesion {
        @include m.boton(darken(v.$naranja, 10%), v.$blanco);
    }
    .dashboard {
        background-color: v.$grisDarkMode;
        .enlace {
            color: darken(v.$grisClaro, 15%);
            &:hover {
                color: darken(v.$grisClaro, 25%);
            }
        }
        .formulario input:not([type=submit]) {
            background-color: lighten(v.$grisDarkMode, 10%);
            color: v.$blanco;
            &::placeholder {
                color: darken(v.$grisClaro, 30%);
            }
        }
    }
    .listado-proyectos .proyecto {
        background-color: lighten(v.$grisDarkMode, 20%);
        &:hover {
            background-color: lighten(v.$grisDarkMode, 10%);
        }
    }
    .filtros input[type="radio"] {
        accent-color: v.$naranja;
    }
    .listado-tareas .tarea p,
    .listado-tareas .no-tareas {
        color: v.$grisClaro;
    }
    .tarea {
        border-bottom: 2px solid lighten(v.$grisDarkMode, 20%);
    }
    .modal .formulario {
        background-color: v.$grisDarkMode;
        legend {
            color: v.$grisClaro;
        }
        input[type="text"] {
            background-color: lighten(v.$grisDarkMode, 10%);
            border: none;
            color: v.$blanco;
            &::placeholder {
                color: darken(v.$grisClaro, 20%);
            }
        }
    }
}