@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar {
    opacity: 0;
    background-color: v.$indigo;
    height: 0;
    overflow: hidden;
    transition-property: opacity;
    transition-duration: 500ms;

    @include m.tablet {
        height: 100%;
        opacity: 1;
        padding: 2rem;
        background-color: v.$indigo;
    }
    
    &.mostrar {
        opacity: 1;
        padding: 2rem;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgb(0 0 0 / .90);
        @include m.tablet {
            display: none;
        }
    }

    .contenedor-sidebar {
        display: flex;
        justify-content: space-between;
        align-items: center;

        h2 {
            color: v.$blanco;
            margin: 0;
            text-align: center;
        }
    }

    .sidebar-nav {
        margin-top: 5rem;

        a {
            color: v.$blanco;
            display: block;
            padding: .7rem;
            margin-bottom: 2rem;
            transition: background-color;
            transition-duration: 300ms;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 2.4rem;
            text-align: center;

            &:hover, &.activo {
                background-color: darken(v.$indigo, 20%);
                border-radius: .5rem;
            }
            
            @include m.tablet {
                font-size: 1.5rem;
                margin-bottom: 1.5rem;
                text-align: left;
            }
        }
    }

    .cerrar-sesion-mobile {
        margin-top: 10rem;

        .cerrar-sesion {
            @include m.boton(v.$indigo, v.$blanco);
        }
        @include m.tablet {
            display: none;
        }
    }
}